@import './mixins.scss';
@import './variables.scss';

.productpage{

    margin-bottom: 40px;

    .popup-box{
        position: fixed;
        color: $white;
        background-color: green;
        width: 300px;
        font-weight: 600;
        font-size: 20px;
        padding: 15px;
        text-align: center;
        border-radius: 7px;
        top: 170px;
        left: 50%;
        margin-left: -150px;
        transition: .3s all ease;
        z-index: 800;

        transform: translateY(-220%);

        &.active{
            transform: translateY(0%);
        }
    }

    .under-characteristcs{
        display: flex;
        justify-content: center;

        .button-characteristics{
            display: flex;
            column-gap: 8px;
            margin-top: 32px;
            padding: 16px;
            border: 1px solid #545454;
            border-radius: 8px;
            align-items: center;

            .icon{
                width: 24px;

                &.active{
                    transform: rotateX(180deg);
                }
            }
        }
    }

    

    &__links{

        display: flex;
        column-gap: 16px;
        align-items: center;

        p{
            overflow-x: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

        a{
            color: $subtitle-color;
        }

        .arrow-link{
            color: $subtitle-color;
            min-width: 24px;
            min-height: 24px;
        }
    }

    .box{
        margin-top: 40px;
        display: flex;
        column-gap: 48px;
    }

    &__images{
        display: flex;
        column-gap: 48px;
        max-height: 516px;

        &-small{
            margin-top: 35px;
            display: flex;
            flex-direction: column;
            row-gap: 24px;

            &-box{
                
                width: 95px;
                max-height: 75px;
                text-align: center;
                opacity: .6;
                padding: 5px;
                cursor: pointer;

                &.active{
                    opacity: 1;
                    border: 1px solid $black-color;
                    border-radius: 6px;
                }

                img{
                    width: auto;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        &-main{
            height: 516px;
            width: 415px;

            img{
                object-fit: contain;
                height: 100%;
                width: 100%;
            }
        }
    }

    &__info{
        max-width: 535px;

        

        &-title{
            font-size: 40px;
            font-family: "SF Pro Display bold";
        }

        &-rating{
            margin-top: 20px;
            display: flex;
            column-gap: 5px;

            p{
                color: rgb(129, 129, 5);
                font-weight: 600;
                font-size: 18px;
            }

            &-star{
                
                margin-top: 2px;
                .star-icon{
                    color: rgba(46, 46, 4, 0.395);  
                }

                .star-icon.active{
                    color: rgb(129, 129, 5);
                }
            }
        }

        &-price{
            margin-top: 24px;
            font-size: 32px;
            font-family: "SF Pro Display medium";
        }

        &-attributes{
            margin-top: 24px;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            column-gap: 10px;
            row-gap: 16px;

            &-item{
                display: flex;
                border-radius: 7px;
                padding: 16px;
                column-gap: 8px;
                background-color: $optional-grey;
                align-items: center;

                .attribute-icon{
                    min-width: 24px;
                    min-height: 24px;
                }

                .attribute-text{
                    display: flex;
                    flex-direction: column;

                    &-name{
                        color: $subtitle-color;
                    }
                }
            }
        }

        &-description{
            margin-top: 24px;
            margin-bottom: 40px;
            font-size: 14px;
            color: $subtitle-color;
            overflow: hidden;
            text-overflow: ellipsis;
            max-height: 72px;
            position: relative;
            transition: 0.4s all ease;

            &.active{
                max-height: 100%;
                overflow: inherit;
            }

            .button-more{
                
                padding: 0 10px 0 10px;
                color: $black-color;
                background-color: $white;
                position: absolute;
                bottom: 0;
                right: 0;
                text-decoration: underline;
                text-align: end;
                cursor: pointer;
            }

            .button-more.active{
                bottom: -20px;
            }
        }

        &-cart{
            color: $white;
            background-color: $black-color;
            border-radius: 6px;
            padding: 16px 56px;
        }

        &-delivery{
            margin-top: 32px;
            display: flex;
            column-gap: 32px;

            &-box{
                display: flex;
                column-gap: 16px;

                &-icon{

                    align-content: center;
                    .icon{
                        width: 24px;
                        height: 24px;
                    }
                    
                }

                &-text{
                    font-size: 14px;

                    .title{
                        color: $subtitle-color;
                    }

                    .subtitle{
                        margin-top: 5px;
                        font-weight: 600;
                    }
                }
            }
        }

        
    }

    &__characteristics{
        margin-top: 120px;
        max-height: 580px;
        overflow: hidden;

        &-title{
            font-size: 24px;
            font-weight: 600;
        }

        .category-title{
            font-size: 20px;
            font-weight: 600;
            margin-top: 40px;
        }

        &-item{
            margin-top: 24px;
            display: flex;
            justify-content: space-between;
            padding-bottom: 8px;
            border-bottom: 0.50px solid #cdcdcd;

            .item-name{
                max-width: 30%;
            }

            .item-value{
                text-align: right;
                word-break: break-word;
                max-width: 40%;
            }
        }

    }

    &__characteristics.active{
        max-height: 100%;
        overflow: inherit;
    }
}

@media(max-width:1150px){

    .productpage{

        &__images{
            &-small{
                &-box{
                    width: 55px;
                    max-height: 75px;
                }
            }
            &-main{
                height: 416px;
                width: 315px;
            }
        }
    }
}

@media(max-width:1000px){

    .productpage{

        &__images{

            max-height: 400px;

            &-main{
                align-self: center;
                width: 215px;
                height: 216px;
            }
        }
    }
}

@media(max-width:900px){

    .productpage{
        margin-top: 40px;

        .box{
            flex-direction: column;
        }

        &__images{
            text-align: center;
            align-items: center;
            max-height: inherit;
            flex-direction: column-reverse;

            &-small{
                flex-direction: row;
                column-gap: 32px;

                &-box{
                    width: 75px;
                    max-height: 95px;
                }
            }

            &-main{
                height: 516px;
                width: 415px;
            }
        }

        &__info{
            margin-top: 54px;
            width: 100%;
        }
    }
}

@media(max-width: 540px){

    .productpage{
        &__info-attributes{
            grid-template-columns: repeat(2, 1fr);
        }
    }
}


@media(max-width:480px){

    .productpage{

        &__images{

            &-small{
                column-gap: 16px;
                &-box{
                    width: 75px;
                    height: 65px;
                }
            }

            &-main{
                width: 265px;
                height: 330px;
            }
        }

        &__info-cart{
            width: 100%;
        }

        &__info-delivery{

            justify-content: center;

            &-box{
                text-align: center;
                flex-direction: column;
            }
        }

        &__info-attributes{
            &-item{
                padding: 16px 8px;
            }
        }
    }
}

@media(max-width:375px){

    .productpage{

        &__images{

            &-small{
                column-gap: 8px;
                &-box{
                    width: 65px;
                    height: 55px;
                }
            }
        }
    }
}