@import './mixins.scss';
@import './variables.scss';

.anyproducts{
    margin-top: 56px;

    &__title{
        font-size: 24px;
        font-weight: 600;
    }

    &__header{
        display: flex;
        column-gap: 32px;

        &-title{
            font-size: 18px;
            font-weight: 600;
            color: $subtitle-color;
        }

        &-title.active{
            color: $black-color;
            border-bottom: 3px solid $black-color;    
        }
    }

    &__items{
        margin-top: 32px;
        display: flex;
        flex-wrap: wrap;
        column-gap: 16px;
        row-gap: 15px;
        justify-content: center;

        &-box{
            display: flex;
            flex-direction: column;
            padding: 24px 16px;
            width: 268px;
            text-align: center;
            align-items: center;
            background: #f6f6f6;
            border-radius: 9px;

            &-image{
                width: 160px;
                height: 160px;
                
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }

            &-title{
                margin-top: 16px;
                font-size: 16px;
                color: $black-color;
                font-weight: 600;
                height: 60px;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            &-price{
                margin-top: 16px;
                font-size: 24px;
                color: $black-color;
                font-weight: 700;
            }

            &-button{
                margin-top: 24px;
                background-color: $black-color;
                color: $white;
                padding: 12px 64px;
                border-radius: 8px;
            }
        }
    }
}

@media(max-width:670px){

    .anyproducts__items-box{

        width: 162px;

        &-image{
            width: 104px;
            height: 104px;
        }

        &-button{
            padding: 12px 42px;
        }
    }
}

@media(max-width:375px){

    .anyproducts__items{

        column-gap: 4px;

        &-box{

            width: 142px;
    
            &-image{
                width: 104px;
                height: 104px;
            }
    
            &-button{
                padding: 12px 22px;
            }
        }
    }

    
}