@import './variables.scss';
@import './mixins.scss';

.banner{
    background-color: $charcoal;

    .container{
        display: flex;
        column-gap: 42px;
        justify-content: space-between;
    }

    &__text{
        display: flex;
        flex-direction: column;
        color: $white;
        vertical-align: middle;
        align-self: center;

        &-subtitle{
            font-size: 25px;
            font-weight:600;
            opacity: .4;
        }

        &-title{
            @include fluid-text(72, 96);
            font-family: 'SF Pro Display thin';

            span{
                font-weight: bold;
            }
        }

        &-description{
            font-size: 18px;
            color: $subtitle-color;
        }

        button{
            @include button-shop-now-w;
            margin-top: 24px;
        }
    }

    &__image{
        margin-top: 73px;
    }
}

@media(max-width:560px){
    .banner{
        .container{
            flex-direction: column;
        }

        &__text{
            margin-top: 88px;
            align-items: center;
            text-align: center;
        }

        &__image{
            align-self: center;
            margin-top: 48px;
            img{
                width: 343px;
                height: 289px;
            }
        }
    }
}