@import './mixins.scss';
@import './variables.scss';

.profile{

    position: relative;

    .popup-box{
        background-color: rgb(12, 179, 12);
        color: $white;
        font-weight: 600;
        font-size: 20px;
        width: 300px;
        text-align: center;
        padding: 15px;
        border-radius: 7px;
        position: fixed;
        transition: .5s all ease;
        transform: translateY(-180%);
        left: 50%;
        margin-left: -150px;

        &.active{
            transform: translateY(0%);
        }
    }

    .error-box{
        background-color: red;
        color: $white;
        font-weight: 600;
        font-size: 20px;
        width: 300px;
        text-align: center;
        padding: 15px;
        border-radius: 7px;
        position: fixed;
        transition: .5s all ease;
        transform: translateY(-250%);
        left: 50%;
        margin-left: -150px;

        &.active{
            transform: translateY(0%);
        }
    }

    .empty-user{
        font-size: 24px;
        font-weight: 600;
        margin-bottom: 40px;
        min-height: 65vh;
    }

    .user-title{
        font-size: 24px;
        font-weight: 600;
        
    }

    &__header{
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        column-gap: 20px;
        margin-bottom: 40px;
        align-content: center;

        .exit-icon{
            color: red;
            width: 32px;
            min-width: 32px;
            cursor: pointer;
            transition: .3s all ease;

            &:hover{
                opacity: .4;
            }
        }
    }

    &__main{

        display: flex;
        flex-direction: column;

        &-image{
            width: 150px;
            height: 150px;
            min-width: 60px;
            min-height: 60px;
            border-radius: 50%;
            border: 1px solid $black-color;
            overflow: hidden;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &-input{
            margin-top: 10px;
            margin-bottom: 20px;
            position: relative;

            #input__file{
                width: 0px;
                opacity: 0;
                visibility: hidden;
                position: absolute;
            }

            label{
                display: flex;
                max-width: 250px;
                align-items: center;
                background-color: green;
                color: $white;
                border-radius: 7px;
                column-gap: 5px;

                .input-icon{
                    border-right: 0.5px solid $white;
                    padding: 15px;
                }

                .file-icon{
                    width: 24px;
                    min-width: 24px;
                    color: $white;
                    
                }
            }

            .send-image{
                background-color: $black-color;
                color: $white;
                padding: 15px;
                border-radius: 7px;
                margin-top: 15px;
            }
        }
    }
}