
@import './mixins.scss';
@import './variables.scss';

.populars{

    .container{
        display: none;
    }

    margin-top: 56px;
    display: flex;
    justify-content: center;

    &__pairone{
        display: flex;
    }

    &__pairtwo{
        display: flex;
    }

    .item{
        display: flex;
        flex-direction: column;

        a{
            color: $black-color;
        }
    }


    .item-text{
        padding-inline: 32px;
    }

    .item-image{
        height: 340px;
        
        margin-bottom: 20px;

        img{
            float: right;
            width: auto;
            height: 100%;
        }
    }

    .item-title{
        font-size: 33px;
        height: 85px;
    }

    .item-description{
        font-size: 14px;
        color: $subtitle-color;
    }

    button{
        margin-top: 16px;
        padding: 16px 56px;
        border-radius: 6px;
        border: 1px solid $black-color;
        font-weight: 600;
        margin-bottom: 20px;
        
    }

    .item.grey{
        background-color: $grey;
    }

    .item.darkgrey{

        .item-title{
            color: $white;
        }

        a{
            color: $white;
        }

        button{
            border: 1px solid $white;
            color: $white;
        }

        background-color: $dark-grey;
    }
}

@media(max-width:900px){

    .populars{
        flex-wrap: wrap;
        row-gap: 50px;

        .item-text{
            text-align: center;
        }

        .item-image{
            float: none;
            text-align: right;
        }


    }

    
}

@media(max-width:600px){

    .populars{

        &__pairone{
            display: none;
        }

        &__pairtwo{
            display: none;
        }

        .container{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;

            .frame{
                width: 420px;
                height: 570px;
                overflow: hidden;
            }

            .slider{
                width: 100%;
                height: 100%;
                display: flex;
                transition: .3s all ease;
                
                .item{

                    width: 420px;
    
                    &-image{
                        width: 420px;
                        text-align: center;
                        img{
                            object-fit: contain;
                        }
                    }
                }
            }

            

            &__bottom{
                display: flex;
                column-gap: 10px;

                .pagination-circle{
                    width: 18px;
                    height: 18px;
                    border: 1px solid $black-color;
                    border-radius: 50%;
                    cursor: pointer;
                    margin-top: 10px;
                }

                .active{
                    background-color: $black-color;
                }
            }
        }
    }
}

@media(max-width:470px){
    .populars{

        .container{

            .frame{
                width: 320px;
                height: 570px;
                overflow: hidden;
            }

            .slider{
                width: 100vw;
                height: 100%;
                display: flex;
                transition: .3s all ease;
                
                .item{
    
                    &-image{
                        width: 320px;
                        text-align: center;
                        img{
                            object-fit: contain;
                        }
                    }
                }
            }
        }
    }
}

@media(max-width:350px){
    .populars{
        .container{
            justify-content: baseline;
        }
    }
}