@import './mixins.scss';
@import './variables.scss';

.overlay-userform{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(124, 117, 117, 0.527);
    
}

.userform{

    position: relative;

    .popup-box{
        background-color: red;
        position: absolute;
        top: 50px;
        padding: 15px;
        color: $white;
        font-weight: 600;
        border-radius: 7px;
        width: 300px;
        left: 50%;
        margin-left: -150px;
        
    }

    .success-box{
        background-color: green;
        position: absolute;
        top: 50px;
        padding: 15px;
        color: $white;
        font-weight: 600;
        border-radius: 7px;
        width: 300px;
        left: 50%;
        margin-left: -150px;
        
    }

    .email-error{
        color: red;
    }

    .password-error{
        color: red;
    }

    background-color: $white;
    position: fixed;
    height: 100vh;
    width: 420px;
    z-index: 999;
    top: 0;
    right: 0;
    align-items: center;
    text-align: center;
    padding-inline: 15px;

    .close-icon{
        position: absolute;
        width: 24px;
        height: 24px;
        top: 10px;
        right: 20px;
        cursor: pointer;
    }

    &-title{
        margin-top: 107px;
        font-size: 40px;
        font-family: 'SF Pro Display bold';
    }

    &__signup{
        margin-top: 40px;
        display: flex;
        flex-direction: column;

        label{
            text-align: left;
            margin-bottom: 5px;
        }

        input{
            padding: 18px 15px;
            border: 1px solid #1e1e1e;
            border-radius: 6px;
            font-size: 18px;
            margin-bottom: 15px;

            &::placeholder{
                font-size: 18px;
            }
        }

        button{
            margin-top: 40px;
            background-color: #1e1e1e;
            padding: 14px 10px;
            color: $white;
            font-weight: 600;
            font-size: 20px;
            border-radius: 6px;
        }
    }

    &__account{
        margin-top: 11px;
        display: flex;
        flex-direction: column;
        row-gap: 5px;

        &-subtitle{
            cursor: pointer;
            font-weight: 600;
        }
    }

}

@media(max-width:420px){
    .userform{
        width: 100%;
    }
}