@import './mixins.scss';
@import './variables.scss';

.categories{
    margin-top: 80px;
    margin-bottom: 80px;

    &__header{

        &-title{
            font-size: 24px;
            font-weight: 600;
        }
    }

    &__items{
        margin-top: 32px;
        display: flex;
        flex-wrap: wrap;
        row-gap: 15px;
        justify-content: center;
        @include clamp-columngap(16,32);

        &-box{
            padding: 24px 10px;
            background-color: $light-grey;
            text-align: center;
            width: 160px;
            border-radius: 15px;

            &-image{
                .icon{
                    color: $black-color;
                    width: 48px;
                }
            }

            &-title{
                margin-top: 8px;
                font-size: 16px;
                font-weight: 600;
                color: $black-color;
            }
        }
    }
}

@media(max-width:420px){

    .categories__items{
        &-box{
            width: 120px;
        }
    }
}