
.header{

    padding: 28px 0;
    position: sticky;
    background-color: $white;
    z-index: 900;
    top: 0;

    .burger-icon{
        width: 28px;
        display: none;
    }

    .header__box-search-mobile{
        display: none;
    }
    
    
    .container{
        display: flex;
        justify-content: space-between;
        column-gap: 16px;
        align-items: center;  
    }

    &__box{
        display: flex;
        align-items: center;
        column-gap: 32px;

        &-search{
            position: relative;
            align-content: center;
            height: 56px;

            input{
                background-color: #F5F5F5;
                height: 100%;
                font-size: 18px;
                padding-left: 30px;
                padding-right: 15px;
                width: 400px;

                &::placeholder{
                    font-size: 18px;
                }
            }

            .search-icon{
                position: absolute;
                left: 0;
                width: 24px;
                aspect-ratio: 1;
                top: 17px;
            }

            &-results{
                z-index: 999;
                position: absolute;
                top: 60px;
                display: flex;
                flex-direction: column;
                row-gap: 25px;
                background-color: $white;
                padding: 15px 20px;
                border: 1px solid $black-color;
                width: 100%;
                max-height: 500px;
                overflow: auto;
                overscroll-behavior: none;

                &-item{
                    display: flex;
                    column-gap: 10px;

                    .item-image{
                        min-width: 94px;
                        min-height: 104px;
                        max-width: 104px;
                        max-height: 104px;

                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: contain;
                        }
                    }

                    .item-title{
                        color: $black-color;
                        font-weight: 600;
                    }
                }
            }
        }
    }

    &__menu{
        display: flex;
        column-gap: 32px;
        align-items: center;

        &-catalogue{
            position: relative;

            &-item{
                display: flex;
                column-gap: 5px;
                align-items: center;
                cursor: pointer;
                transition: .3s all ease;
    
                p{
                    font-size: 22px;
                }
    
                .arrow-down{
                    width: 22px;
                    align-self: flex-end;
                    transition: .2s all ease;
                }

                .arrow-down.active{
                    transform: rotateX(180deg);
                }

                &:hover{
                    opacity: .4;
                }
            }
        }

        .cart{
            position: relative;
            .cart-icon{
                width: 28px;
                aspect-ratio: 1;
                color: $black-color;
                cursor: pointer;
            }

            .cart-items{
                position: absolute;
                background-color: $black-color;
                color: $white;
                border-radius: 50%;
                align-items: center;
                display: flex;
                justify-content: center;
                width: 25px;
                height: 25px;
                right: -10px;
                top: 15px;
            }
        }

        .profile-box{
            width: 32px;
            aspect-ratio: 1;
            min-width: 32px;
            border-radius: 50%;
            overflow: hidden;

            .profile-icon{
                width: 100%;
                height: 100%;
                object-fit: cover;
                cursor: pointer;
            }
        }

        

        
    }

}

.catalogue-dropdown{
                
    position: absolute;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    background-color: $white;
    padding: 20px 25px;
    border: 1px solid $black-color;
    border-radius: 10px;
    right: 0;
    top: 40px;
    font-size: 20px;

    a{
        color: $black-color;

        &:hover{
            color: $subtitle-color;
        }
    }

    
}

.overlay{
    background-color: rgba(227, 219, 219, 0.3);
    width: 100%;
    position: absolute;
    left: 0;
    height: 100%;
}

.catalogue-dropdown.active{
    display: flex;
}

.header__mobile{

    background-color: $black-color;
    padding: 20px 30px;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    z-index: 999;
    width: 250px;
    color: $white;
    transition: .3s all ease-in-out;
    transform: translateX(120%);

    .close-icon{
        position: absolute;
        right: 10px;
        top: 10px;
        width: 24px;
        color: $white;
        z-index: 999;
    }

    &-menu{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        //row-gap: 15px;

        a{
            color: $white;
        }

        .title{
            font-size: 24px;
            font-weight: 600;
        }

        .menu-categories{
            margin-left: 10px;
            margin-top: 20px;
            display: flex;
            flex-direction: column;
            row-gap: 10px;

            a{
                color: $white;
            }
        }

        .cart-mobile{
            display: flex;
            column-gap: 5px;
            margin-top: 30px;
            align-items: center;
            position: relative;

            .cart-icon{
                width: 26px;
            }

            .cart-item{
                background-color: $white;
                color: $black-color;
                padding: 2px;
                margin-left: 10px;
            }
        }

        .profile{
            display: flex;
            margin-top: 10px;
            column-gap: 5px;
            align-items: center;

            &-icon{
                width: 24px;
            }
        }
    }
}

.header__mobile.active{
    transform: translateX(0%);
}

@media(width <= 940px){
    .header__box-search{
        input{
            width: 250px;
        }
    }
}

@media(max-width: 750px){

    .header__box-search{
        input{
            width: 350px;
        }
    }

    .header__menu{

        .cart{
            display: none;
        }
    
        .profile{
            display: none;
        }
    
        &-catalogue{
            display: none;
        }
    
        .burger-icon{
            display: initial !important;
        }
    }
    
}

@media(max-width:700px){

    .header__box-search{
        input{
            width: 250px;
        }
    }
}

@media(width <= 630px){
    .header__box-search{

        &-results-item{
            .item-image{
                min-width: 64px;
                min-height: 64px;
                max-width: 74px;
                max-height: 74px;
            }
        }
    }
}

@media(max-width:580px){
    .header__box-search{
        input{
            width: 200px;
        }
    }
}

@media(max-width:510px){

    .header__box-search{
        display: none;
    }

    .header__box-search-mobile{

        display: inherit !important;
        position: relative;
        height: 56px;
        //padding-inline: 15px;
        width: 100%;
        
        margin-top: 10px;

        input{
            background-color: #F5F5F5;
            height: 100%;
            width: 300px;
            font-size: 18px;
            padding-left: 30px;
            padding-right: 15px;
            margin-left: 45px;

            &::placeholder{
                font-size: 18px;
            }
        }

        .search-icon{
            position: absolute;
            left: 45px;
            width: 24px;
            aspect-ratio: 1;
            top: 17px;
        }
    }
}

@media(width <= 375px){

    .header__box-search-mobile{

        input{
            margin-left: 15px;
            
        }
        .search-icon{
            left: 15px;
        }

        .header__box-search-results{
            max-height: 280px;
        }
    }
}

