@import './mixins.scss';
@import './variables.scss';

.footer{

    background-color: $black-color;

    &__head{
        padding-top: 104px;
        display: flex;
        justify-content: space-between;
        font-size: 14px;
        color: $subtitle-color;
    }

    &__logo{

        p{
            max-width: 384px;
            margin-top: 24px;
        }
    }

    &__actions{

        .title{
            font-size: 16px;
            color: $white;
        }

        &-list{
            margin-top: 10px;
            display: flex;
            flex-direction: column;
            row-gap: 10px;
        }
    }

    
}

@media(max-width:920px){

    .footer{

        &__head{
            padding-top: 48px;
        }

        &__logo{
            p{
                width: 200px;
            }
        }
    }
}

@media(max-width:740px){

    .footer{

        &__head{
            flex-direction: column;
            row-gap: 32px;
            align-items: center;
            text-align: center;
        }

        &__logo{
            p{
                width: 100%;
            }
        }
    }
}