@import './mixins.scss';
@import './variables.scss';

.productpage__reviews{

    margin-top: 120px;

    &-title{
        font-size: 24px;
        font-weight: 600;
    }

    

    &-comment{

        &-star{
            margin-top: 30px;
            display: flex;
            column-gap: 10px;

            .reviews-mark{
                cursor: pointer;
                width: 24px;
                color: rgba(46, 46, 4, 0.395);

                &.active{
                    color: rgb(129, 129, 5);
                }
            }
        }

        &-textarea{
            margin-top: 20px;
            border: 0.50px solid #cecece;
            border-radius: 7px;
            padding: 24px 16px;
            resize:vertical;
            width: 100%;
            height: auto;
        }

        .send-comment{
            margin-top: 10px;
            border: 1px solid #545454;
            padding: 12px;
            border-radius: 8px;

            &:disabled{
                cursor: auto;
                opacity: .5;
            }
        }
    }

    &-users{
        display: flex;
        flex-direction: column;
        row-gap: 24px;
        margin-top: 32px;

        &.current{
            border-bottom: 0.5px solid #cecece;

            .title{
                font-size: 24px;
                font-weight: 600;
            }
        }

        .title{
            margin-top: 20px;
            font-size: 24px;
            font-weight: 600;
        }

        .empty-reviews{
            font-size: 20px;
            font-weight: 600;
        }

        &-review{
            display: flex;
            padding: 24px 0;
            column-gap: 19px;

            &-image{
                width: 56px;
                height: 56px;
                min-width: 56px;
                min-height: 56px;
                border-radius: 50%;
                overflow: hidden;

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &-info{
                display: flex;
                flex-direction: column;
                row-gap: 8px;

                .info-username{
                    font-size: 17px;
                    font-weight: 600;
                }

                .info-text{
                    font-size: 15px;
                    color: $subtitle-color;
                }

                .info-mark{
                    .star{

                        color: rgba(46, 46, 4, 0.395);

                        &.active{
                            color: rgb(129, 129, 5);
                        }
                    }
                }
            }
        }
    }
}