@import './variables.scss';

@mixin button-shop-now-w{

    color: $white;
    font-weight: medium;
    border: 1px solid $white;
    border-radius: 6px;
    padding: 16px 56px;
    width: 184px;
    height: 56px;
}

@mixin button-shopn-now-b{

    color: $white;
    font-weight: medium;
    border: 1px solid $black-color;
    border-radius: 6px;
    padding: 16px 56px;
    width: 184px;
    //height: 56px;
    text-align: center;
}

@mixin border-bottom-menu{
    padding-bottom: 12px;
    border-bottom: 0.5px solid #b5b5b5;
}


@mixin clamp-padding($min, $max){
    padding-inline: clamp(#{$min}px, #{$max/1920*100}vw, #{$max}px);
}

@mixin clamp-columngap($min, $max){
    column-gap: clamp(#{$min}px, #{$max/1440*100}vw, #{$max}px);
}

@mixin fluid-text($min, $max){
    font-size: clamp(#{$min}px, #{$max/1440*100}vw, #{$max}px);
}



