@import './mixins.scss';
@import './variables.scss';

.empty-cart{
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
    min-height: 65vh;
}

.cart{
    display: flex;
    column-gap: 48px;


    &__products{
        width: 50%;
        display: flex;
        flex-direction: column;

        &-title{
            font-size: 24px;
            font-weight: 600;
        }

        &-item{
            display: flex;
            padding: 40px 0px;
            border-bottom: 0.5px solid #a3a3a3;

            &-image{

                min-width: 80px;
                min-height: 80px;
                max-width: 90px;
                max-height: 90px;

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }

            &-info{
                width: 100%;
                display: flex;
                column-gap: 10px;
                align-items: center;

                .info-title{
                    margin-left: 15px;
                    max-width: 195px;
                    font-weight: 600;
                    line-height: 150%;
                }

                &-price{
                    width: 100%;
                    display: flex;
                    column-gap: 20px;
                    justify-content: space-between;
                    align-items: center;

                    .info-quantity{
                        display: flex;
                        column-gap: 8px;
                        
                        .icons-quantity{
                            min-width: 18px;
                            cursor: pointer;
                        }
    
                        .quantity{
                            padding: 8px 16px;
                            border: 0.50px solid #d9d9d9;
                        }
                    }
    
                    .info-price{
                        font-weight: 600;
                        font-size: 20px;
                    }
    
                    .icon-delete{
                        cursor: pointer;
                        min-width: 24px;
                        height: 24px;
                    }
                }

                
            }
        }
    }

    &__summary{
        display: flex;
        flex-direction: column;
        width: 50%;
        padding: 56px 64px;

        &-title{
            font-size: 20px;
            font-family: "SF Pro Display bold";
        }

        &-info{
            margin-top: 40px;
            display: flex;
            flex-direction: column;

            label{
                font-size: 14px;
                color: $subtitle-color;
                margin-bottom: 8px;
            }

            input{
                padding: 16px;
                border: 0.50px solid #9f9f9f;
                border-radius: 7px;
                margin-bottom: 24px;
            }
        }

        &-price{
            display: flex;
            flex-direction: column;
            row-gap: 16px;

            .info-price{
                display: flex;
                justify-content: space-between;
                
                

                .title{
                    font-weight: 600;
                }

                .price{
                    font-weight: 600;
                }
            }

            .dop{
                display: flex;
                justify-content: space-between;

                .subtitle{
                    color: $subtitle-color;
                }

                .price{
                    font-weight: 600;
                }
            }
        }

        .button-buy{
            margin-top: 48px;
            border-radius: 6px;
            background-color: $black-color;
            color: $white;
            padding: 16px 0px;
        }
    }

    
}

@media(max-width:1050px){
    .cart{
        &__products{

            &-item-info{
                flex-direction: column;
                align-items: start;

                .info-title{
                    max-width: 100%;
                }

                &-price{
                    margin-top: 15px;
                    margin-left: 10px;
                }
            }
        }
    }
}

@media(max-width:900px){
    .cart{
        flex-direction: column;

        &__products{
            width: 100%;
        }

        &__summary{
            width: 100%;
        }
    }
}

@media(max-width:520px){

    .cart{

        &__summary{
            padding: 56px 15px;
        }
    }
}