@import './mixins.scss';
@import './variables.scss';

.secondbanner{
    display: flex;

    .mobile{
        display: none;
    }

    &__left{
        display: flex;
        flex-direction: column;
        width: 50%;

        &-top{
            display: flex;
            
            &-text{

                align-self: center;

                .title{
                    @include fluid-text(34, 49);
                    font-weight: bold;
                }
                .subtitle{
                    margin-top: 16px;
                    width: 340px;
                    line-height: 171%;
                    color: $subtitle-color;
                    margin-bottom: 30px;
                }
            }

            

        }

        &-bottom{
            display: flex;

            &-left{
                display: flex;
                justify-content: space-between;
                column-gap: 20px;
                background: #ededed;
                width: 50%;
                padding-right: 48px;

                &-text{
                    display: flex;
                    flex-direction: column;
                    align-self: center;
                    .title{
                        font-size: 29px;
                        font-family: 'SF Pro Display light';

                        span{
                            font-weight: 600;
                        }
                    }

                    .subtitle{
                        font-size: 14px;
                        color: $subtitle-color;
                        line-height: 171%;
                    }
                }

                &-image{
                    align-content: center;
                }
            }

            &-right{

                width: 50%;
                display: flex;
                background: #353535;
                padding-right: 48px;
                column-gap: 22px;

                &-image{
                    margin-top: 40px;
                    img{
                        width: 312px;
                        height: 192px;
                    }
                }

                &-text{
                    display: flex;
                    align-self: center;
                    flex-direction: column;
                    

                    .title{
                        color: $white;
                        font-size: 29px;
                        font-family: 'SF Pro Display light';

                        span{
                            font-weight: 600;
                        }
                    }

                    .subtitle{
                        margin-top: 8px;
                        font-size: 14px;
                        color: $subtitle-color;
                        line-height: 171%;
                    }
                }
            }

        }


    }

    &__right{
        display: flex;
        width: 50%;
        justify-content: space-between;
        background: #ededed;

        &-text{
            display: flex;
            flex-direction: column;
            align-self: center;
            padding-left: 56px;
            width: 360px;

            .title{
                
                @include fluid-text(34, 64);
                font-family: 'SF Pro Display light';

                span{
                    font-weight: 600;        
                }   
            }

            .subtitle{
                margin-top: 16px;
                font-size: 14px;
                color: $subtitle-color;
                line-height: 171%;
            }

            button{
                margin-top: 16px;
                @include button-shopn-now-b;
                color: $black-color;
            }
        }

        &-image{
            align-content: center;
        }
    }
}

@media(max-width:1100px){

    .secondbanner{
        flex-direction: column;

        &__left{
            width: 100%;
        }

        &__right{
            width: 100%;
        }
    }
}

@media(max-width:640px){
    .secondbanner{
        &__left{
            &-bottom{
                flex-direction: column;

                &-left{
                    width: 100%;
                }
                &-right{
                    width: 100%;
                    &-image{
                        width: 343px;
                    }
                }
            }
        }
    }
}

@media(max-width:560px){
    .secondbanner{
        .desktop{
            display: none;
        }

        .mobile{
            display: initial;
            margin-bottom: 24px;
        }

        &__left{

            &-top{
                order: 1;
                flex-direction: column;
                text-align: center;
                padding: 40px 15px;
                
            }

            &-bottom{

                &-left{
                    flex-direction: column;
                    text-align: center;
                    padding-right: initial;
                    padding: 40px 15px;
                }

                &-right{
                    flex-direction: column;
                    text-align: center;
                    padding-right: initial;
                    padding: 40px 15px;

                    &-image{
                        margin-top: 0;
                        align-self: center;
                        img{
                            width: auto;
                            height: auto;
                        }
                    }
                }
            }
            
        }

        &__right{
            flex-direction: column-reverse;
            text-align: center;
            align-items: center;
            padding: 40px 0;

            &-text{
                margin: 0 auto;
                padding-left: 0;
                width: 100%;
                padding-inline: 10px;
            }

            button{
                width: 90%;
                align-self: center;
            }

        }

    }

}

@media(max-width:375px){

    .secondbanner{

        .subtitle{
            width: 100%;
            padding-inline: 10px;
        }

        &__left-bottom-right{

            &-image{
                width: 100%;
                img{
                    width: 260px;
                }
            }
        }
    }
}


