@import './mixins.scss';
@import './variables.scss';

.bannersale{
    background: linear-gradient(90deg, rgba(46,46,46,1) 0%, rgba(0,0,0,1) 100%);
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-top: 40px;

    .mobile{
        display: none;
    }

    &__text{
        text-align: center;
        align-content: center;

        .title{
            color: $white;
            @include fluid-text(48, 72);
            font-family: 'SF Pro Display thin';
        }

        span{
            font-weight: 700;
        }

        .subtitle{
            color: #787878;
        }

        button{
            color: $white;
            border: 1px solid $white;
            border-radius: 6px;
            padding: 16px 56px;
            margin-top: 40px;
        }
    }
}

@media(max-width:900px){

    .bannersale{

        .desktop{
          
            width: 250px;
            height: 250px;
            
        }
    }
}

@media(max-width:600px){
    .bannersale{

        flex-direction: column;
        align-items: center;

        .desktop{
            display: none;
        }

        .mobile{
            display: inherit;
        }
    }
}
