@import './mixins.scss';
@import './variables.scss';

.categorypage{

    &__links{

        display: flex;
        column-gap: 16px;
        align-items: center;

        a{
            color: $subtitle-color;
        }

        .arrow-link{
            color: $subtitle-color;
            width: 24px;
            height: 24px;
        }
    }

    &__filters-button{
        margin-top: 40px;
        display: none;

        button{
            width: 164px;
            
            text-align: left;
            vertical-align: middle;
            align-content: center;
            padding: 16px;
            border: 0.50px solid #d4d4d4;
            border-radius: 8px;

            .filter-icon{
                min-width: 24px;
                min-height: 24px;
                vertical-align: middle;
                float: right;
            }

        }
  
    }

    &__head{
        display: flex;
        column-gap: 16px;

        &-filter{
            min-width: 256px;
            max-height: 1200px;

            &.mobile{
                display: none;
            }
        }

        &-products{
            
            display: flex;
            width: 100%;
            

            .products__items{
                
                display:flex;
                flex-wrap: wrap;
                justify-content: center;
                margin-top: 32px;
                column-gap: 16px;
                row-gap: 15px;

                &-box{
                    display: flex;
                    flex-direction: column;
                    padding: 24px 16px;
                    width: 268px;
                    text-align: center;
                    align-items: center;
                    background: #f6f6f6;
                    border-radius: 9px;

                    &-image{
                        width: 160px;
                        height: 160px;
                        overflow: hidden;
                        
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: contain;
                        }
                    }
        
                    &-title{
                        margin-top: 16px;
                        font-size: 16px;
                        color: $black-color;
                        font-weight: 600;
                        height: 60px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
        
                    &-price{
                        margin-top: 16px;
                        font-size: 24px;
                        color: $black-color;
                        font-weight: 700;
                    }
        
                    &-button{
                        margin-top: 24px;
                        background-color: $black-color;
                        color: $white;
                        padding: 12px 64px;
                        border-radius: 8px;
                    }
                }
            }
            
            
        }
    }

    &__pagination{
        display: flex;
        justify-content: center;
        column-gap: 16px;
        margin-bottom: 15px;
        margin-top: 40px;

        .icons-arrow{
            width: 24px;
            height: 24px;
            align-self: center;
            cursor: pointer;
        }

        &-numbers{
            display: flex;
            column-gap: 8px;
            align-items: center;
            

            .pagination-items{
                cursor: pointer;
                width: 32px;
                height: 32px;
                text-align: center;
                align-content: center;
                border-radius: 5px;
            }

            .pagination-items.active{
                background-color: $black-color;
                color: $white;
            }
        }
    }

    .filter{

        &-title{
            font-size: 18px;
            font-weight: 600;
        }

        &-subtitle{
            font-size: 14px;
            color: $subtitle-color;
        }

        &-icons{
            width: 24px;
            height: 24px;
        }

        &-head{
            display: flex;
            justify-content: space-between;
            @include border-bottom-menu;
            margin-top: 24px;
        }

        &__price{

            &-menu{
                margin-top: 24px;

                &-text{
                    display: flex;
                    justify-content: space-between;
                }

                &-items{
                    margin-top: 8px;
                    display: flex;
                    justify-content: space-between;

                    .price-from{
                        border: 0.50px solid #9f9f9f;
                        border-radius: 3px;
                        padding: 8px;
                        width: 107px;
                        height: 40px;
                    }

                    .price-to{
                        border: 0.50px solid #9f9f9f;
                        border-radius: 3px;
                        padding: 8px;
                        width: 107px;
                        height: 40px;
                        text-align: right;
                    }
                }
            }
        }

        &__brands-menu{
            display: flex;
            flex-direction: column;
            row-gap: 8px;
            margin-top: 24px;
            max-height: 335px;
            overflow-y: auto;

            &-item{
                display: flex;
                column-gap: 5px;
            }

            .item-title{
                font-size: 16px;
                font-weight: 600;
            }
        }


    }

    .no-results{

        img{
            width: 256px;
            height: 256px;
        }

        p{
            font-weight: 600;
        }

        align-items: center;
        text-align: center;
        display: flex;
        flex-direction: column;
        row-gap: 10px;
        margin-top: 40px;
    }

}

@media(width <= 1330px){

    .categorypage{
        .products__items{
            &-box{
                width: 230px;
            }
        }
    }
}


@media(width <=920px){

    .categorypage{

        &__filters-button{
            display: flex;
        }

        &__head{

            &-filter{
                display: none;
            }

            &-filter.mobile{
                
                background-color: $white;
                display: flex;
                padding: 0 20px;
                top: 0;
                left: 0;
                width: 310px;
                height: 100vh;
                flex-direction: column;
                position: fixed;
                z-index: 999;
                transition: .3s all ease;
                transform: translateX(-120%);

                .button-back{
                    margin-top: 100px;
                    display: flex;
                    column-gap: 16px;
                    align-items: center;

                    p{
                        font-size: 24px;
                        font-weight: 600;
                    }

                    .back-icon{
                        min-width: 24px;
                        min-height: 24px;
                    }
                }

                &.active{
                    transform: translateX(0%);
                }
                 
            }
        }
    }
}

@media(width<=570px){

    .categorypage{

        .products__items-box{

            width: 162px;
    
            &-image{
                width: 104px;
                height: 104px;
            }
    
            &-button{
                padding: 12px 42px;
            }
        }
    }
}

@media(width <= 420px){
    .categorypage{

        &__head-filter.mobile{
            width: 100%;
        }
    }
}

@media(max-width:375px){

    .categorypage{

        .products__items{

            column-gap: 4px;

            &-box{

                width: 142px;
        
                &-image{
                    width: 104px;
                    height: 104px;
                }
        
                &-button{
                    padding: 12px 22px;
                }
            }
        }
    }

    
}